.useful-links {
    width: 24%;
}

.useful-links-title {
    font-size: 19px;
    padding: 1% 0;
    margin-bottom: 4%;
}

.useful-links-title h5 {
    margin-bottom: 1%;
}

.useful-links-title div {
    width: 25%;
    height: 3px;
    background-color: rgb(202, 6, 202);
    border-radius: 10px;
}

.useful-links nav li {
    margin-bottom: 6%;
}

.useful-links nav li a {
    color: #ffffffd0;
}

/* Responsive */

@media screen and (max-width:1070px) {
    .useful-links {
        width: 400px;
    }
}

@media screen and (max-width:501px) {

    .useful-links {
        width: 90%;
    }
}